import { createClient } from '@supabase/supabase-js';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState} from 'react';
import '../css/style.css'; // Import your CSS file

const supabase = createClient(
    "https://mnzfcjprborefxmgftmc.supabase.co",
    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6Im1uemZjanByYm9yZWZ4bWdmdG1jIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MDAxNTQ2MTUsImV4cCI6MjAxNTczMDYxNX0.AE4mQqW1c2bVWw5l9H2aC7jOKWLtqJMViQ34EKsuOSM"
)

function Success() {
    const [user, setUser] = useState({});
    const navigate = useNavigate();

    useEffect(() => {
        async function getUserData() {
            await supabase.auth.getUser().then((value) => {
                // value.data.user
                if(value.data?.user) {
                    console.log(value.data.user)
                    setUser(value.data.user);
                }
            })
        }
        getUserData();
    }, []);

    async function signOutUser() {
        const { error } = await supabase.auth.signOut();
        navigate("/")
    }

    return (
      <div className="App">
        <div className="container">
            { Object.keys(user).length !== 0 ? 
                <> 
                    <div className="bar">
                    <div className="overlay"></div>
                    </div>
                    <div className="container">
                    <div className="overlay"></div>
                    <title>Logged In - TristyMP4's Website</title>
                        <div className="content">
                        <h1>Logged in successfully!</h1>
                        <div className="button-box">
                            <button onClick={() => signOutUser()}>Sign Out</button>
                            <button onClick={() => { window.location.href = 'https://drive.tristymp4.xyz/secret-hehe' }}>Secret</button>
                        </div>
                        </div>
                    </div>
                </>
            :
                <>
                    <div className="bar">
                    <div className="overlay"></div>
                    </div>
                    <div className="container">
                    <div className="overlay"></div>
                    <title>Unauthorized - TristyMP4's Website</title>
                        <div className="content">
                        <h1>Unauthorized: User is not logged in</h1>
                        <div className="button-box">
                            <button onClick={() => { navigate("/login") }}>Login here</button>
                        </div>
                        </div>
                    </div>
                </>
            }
        </div>
      </div>
    );
  }
  
  export default Success;