import styled from 'styled-components';
import {Link} from 'react-router-dom';


const LinkStyles = styled(Link)`
  position: relative;
  background-color: var(--text-color-light); /* Use the same blue as "TristyMP4" text */
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  cursor: pointer;
  font-size: 1em;
  text-transform: uppercase;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.5), inset 0 0 20px rgba(255, 255, 255, 0.5); /* White glow effect */
  outline: none; /* Remove default outline */
`;



const NavBar = () => (
    <div className="bar">
     <LinkStyles  to="/">Home</LinkStyles>
     <LinkStyles  to="/login">Login</LinkStyles>
    </div>
  );

  export default NavBar;